
.index {
  background: #323231;
  -webkit-transition: background-color 1s ease;
  transition: background-color 1s ease;
}
.index.active {
    background: #0B1234;
}
.index.active canvas.bg-canvas {
      opacity: 1;
}
canvas.bg-canvas {
  position: absolute;
  top: 0;
  opacity: 0;
  background: #0B1234;
  -webkit-transition: opacity 2s ease;
  transition: opacity 2s ease;
}
.david img {
  max-width: 25rem;
  max-height: 25rem;
}
.hank {
  position: absolute;
}
.hank img {
    height: 20rem;
}
.hank, .david, .disconnected {
  width: 100vw;
  height: 100vh;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
}
.hank img, .david img, .disconnected img {
    z-index: 9;
}
.disconnected img {
  border-radius: 2rem;
  cursor: pointer;
  -webkit-transform: rotate(45deg);
          transform: rotate(45deg);
  -webkit-transition: all 1s ease;
  transition: all 1s ease;
  height: 25rem;
}
.disconnected img:hover {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
}
.dg .title {
  font-size: 1rem;
  margin: 0;
}



